body {
    font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';

}

.button-primary {
    background-color: #28a745;
    border-color: #28a745;
    margin-top: .5rem;

}

.no-result {
    position: absolute;
    top: 45%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    font-size: 2rem;
}

.card {
    margin-bottom: 20px !important;
}

.course-length {
    position: absolute;
    bottom: 3rem;
}

.total-courses-text {
    margin-left: 8rem;
    padding: .5rem 2rem 2rem 2rem;
}

.custom-dropdown {
    -webkit-transform: translate3d(0px, 38px, 0px) !important;
            transform: translate3d(0px, 38px, 0px) !important;
}

.text-muted {
    color: #00953A !important;
}

.list-unstyled {
    max-height: 300px;
    overflow: scroll;
    overflow-x: hidden;
}

.search-form {
    display: -webkit-inline-flex;
    display: inline-flex;
}

.search-filter {
    background: transparent;
    border: 1px solid #00B746;
    color: white
}

.search-filter:hover {
    background: transparent;
    border: 1px solid #00B746;
    color: white
}

.course-card {
    width: 15rem;
    -webkit-flex: inherit !important;
            flex: inherit !important;
    height: 18rem;
    border: 0;
    box-shadow: 2px 3px 10px 1px #eee;
    cursor: pointer;
}

.search-box {
    width: 40rem;
}

.course-card:hover {
    box-shadow: 2px 3px 10px 1px #aaa;
}

.jumbotron {
    box-shadow: 2px 3px 10px 1px #eee;
    padding: 2rem 1rem;
    width: 87%;
    margin-right: auto;
    margin-left: auto;
    background-color: #F8F9FA
}
.ipl-progress-indicator.available {
    opacity: 0;
}

.ipl-progress-indicator {
    background-color: #f5f5f5;
    width: 100%;
    height: 100%;
    position: fixed;
    opacity: 1;
    pointer-events: none;
    transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
    z-index: 9999;
}

.insp-logo-frame {
    display: -webkit-flex;
    display: -moz-flex;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    flex-direction: column;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    justify-content: center;
    -webkit-animation: fadein 436ms;
    animation: fadein 436ms;
    height: 98%;
}

.insp-logo-frame-img {
    width: 112px;
    height: 112px;
    -webkit-align-self: center;
    align-self: center;
    border-radius: 50%;
}

.ipl-progress-indicator-head {
    background-color: #c6dafc;
    height: 4px;
    overflow: hidden;
    position: relative;
}

.ipl-progress-indicator .first-indicator,
.ipl-progress-indicator .second-indicator {
    background-color: rgb(0, 149, 58);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
    -webkit-transform-origin: left center;
    transform-origin: left center;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
}

.ipl-progress-indicator .first-indicator {
    -webkit-animation: first-indicator 2500ms linear infinite;
    animation: first-indicator 2500ms linear infinite;
}

.ipl-progress-indicator .second-indicator {
    -webkit-animation: second-indicator 2500ms linear infinite;
    animation: second-indicator 2500ms linear infinite;
}

.ipl-progress-indicator .insp-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    border-radius: 50%;
    -webkit-align-self: center;
    align-self: center;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }

    ;
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }

    ;
}

@-webkit-keyframes first-indicator {
    0% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    25% {
        -webkit-transform: translate(0%) scaleX(0.5);
                transform: translate(0%) scaleX(0.5);
    }

    50% {
        -webkit-transform: translate(25%) scaleX(0.75);
                transform: translate(25%) scaleX(0.75);
    }

    75% {
        -webkit-transform: translate(100%) scaleX(0);
                transform: translate(100%) scaleX(0);
    }

    100% {
        -webkit-transform: translate(100%) scaleX(0);
                transform: translate(100%) scaleX(0);
    }

    ;
}

@keyframes first-indicator {
    0% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    25% {
        -webkit-transform: translate(0%) scaleX(0.5);
                transform: translate(0%) scaleX(0.5);
    }

    50% {
        -webkit-transform: translate(25%) scaleX(0.75);
                transform: translate(25%) scaleX(0.75);
    }

    75% {
        -webkit-transform: translate(100%) scaleX(0);
                transform: translate(100%) scaleX(0);
    }

    100% {
        -webkit-transform: translate(100%) scaleX(0);
                transform: translate(100%) scaleX(0);
    }

    ;
}

@-webkit-keyframes second-indicator {
    0% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    60% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    80% {
        -webkit-transform: translate(0%) scaleX(0.6);
                transform: translate(0%) scaleX(0.6);
    }

    100% {
        -webkit-transform: translate(100%) scaleX(0.1);
                transform: translate(100%) scaleX(0.1);
    }

    ;
}

@keyframes second-indicator {
    0% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    60% {
        -webkit-transform: translate(0%) scaleX(0);
                transform: translate(0%) scaleX(0);
    }

    80% {
        -webkit-transform: translate(0%) scaleX(0.6);
                transform: translate(0%) scaleX(0.6);
    }

    100% {
        -webkit-transform: translate(100%) scaleX(0.1);
                transform: translate(100%) scaleX(0.1);
    }

    ;
}
